.feedback__check label {
  display: flex;
  padding-left: 30px;
  cursor: pointer;
  position: relative;
  min-height: 20px;
  margin-bottom: 40px;
}
.feedback__check label input[type=checkbox]:checked + .feedback__sort::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-repeat: no-repeat;
  top: 5px;
  left: 5px;
}
.feedback__check label:hover > .feedback__sort, .feedback__check label:focus > .feedback__sort {
  background-color: transparent;
  outline: 1px solid #ffffff;
}
.feedback__check input {
  display: none;
}

.feedback__check input[type=checkbox]:disabled + .feedback__sort {
  background-color: rgba(255, 255, 255, 0.6);
  border: 1px solid rgb(255, 255, 255);
}

.feedback__check input[type=checkbox]:disabled + .feedback__check-error {
  display: none;
}

.feedback__check input[type=checkbox]:checked:disabled + .feedback__sort {
  background-color: #a9afb7;
  border: 1px solid #a9afb7;
  cursor: default;
}

.feedback__check input[type=checkbox]:checked:disabled + .feedback__sort::before {
  background-image: url("../img/svg/check.svg");
}

.feedback__check input[type=checkbox]:checked + .feedback__sort {
  background-color: #ffffff;
}

.feedback__check:hover input[type=checkbox]:not(:disabled):checked + .feedback__sort,
.feedback__check:focus input[type=checkbox]:not(:disabled):checked + .feedback__sort {
  background-color: #316dc2;
  outline: 1px solid #ffffff;
}

.feedback__check:hover input[type=checkbox]:not(:disabled):checked + .feedback__sort::before,
.feedback__check:focus input[type=checkbox]:not(:disabled):checked + .feedback__sort::before {
  background-image: url("../img/svg/check.svg");
}

.feedback__check input[type=checkbox]:checked + .feedback__sort::before {
  background-image: url("../img/svg/check-blue.svg");
}

