.opinions {
  padding: 60px 0 131px;
  display: flex;
  flex-direction: column;

  & h2 {
    max-width: 1270px;
    width: 100%;
    font-size: 50px;
    line-height: 50px;
    font-weight: 400;
    color: #316dc2;
    text-align: start;
    align-self: center;
    padding: 0 15px;
    margin: 0;
    letter-spacing: -1.5px;
    margin-bottom: 46px;
  }
}

.opinions__slider {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  margin-bottom: 50px;
}

.opinions__slide {
  display: flex;
  gap: 25px;
  flex-direction: column;
  padding: 50px 50px 40px;
  border: 1px solid #316dc2;
  border-radius: 16px;
  min-height: 430px;
  max-width: 604px;
  height: auto;
}

.opinions__slide-data {
  display: flex;
  min-height: 90px;
  align-items: center;
  justify-content: space-between;

  & span {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 105px;
    font-size: 18px;
    line-height: 24px;
    color: #316dc2;
    max-width: 100px;
    letter-spacing: -0.5px;

    &::before {
      position: absolute;
      content: "";
      width: 90px;
      height: 90px;
      background-repeat: no-repeat;
      left: 0;
    }
  }
}

.opinions__slide-terms {
  display: flex;
  gap: 27px;
  flex-wrap: wrap;
  max-width: 50%;

  & dl {
    display: flex;
    flex-direction: column;
    gap: 8px;
    max-width: 130px;
    margin: 0;

    & dt {
      font-size: 12px;
      line-height: 12px;
      color: #316dc2;
      text-transform: uppercase;
      letter-spacing: -0.5px;
    }

    & dd {
      font-size: 14px;
      line-height: 14px;
      margin: 0;
      letter-spacing: -0.5px;
    }
  }
}

.opinions__slide-text {
  display: flex;
  flex-direction: column;
  gap: 15px;

  & h3 {
    font-size: 24px;
    font-weight: 400;
    color: #316dc2;
    margin: 0;
    letter-spacing: -0.7px;
  }

  & p {
    margin: 0;
    letter-spacing: -0.5px;
  }
}

.opinions__slide--sarah {
  & span {
    &::before {
      background-image: url("../img/sarah.webp");
      background-size: cover;

      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/sarah@2x.webp");
        background-size: cover;
      }
    }
  }
}

.opinions__slide--david {
  & span {
    &::before {
      background-image: url("../img/david.webp");
      background-size: cover;

      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/david@2x.webp");
        background-size: cover;
      }
    }
  }
}

.opinions__control {
  position: relative;
  order: 3;
  display: flex;
  width: 100%;
  max-width: 1270px;
  padding: 0 15px;
  margin: 0 auto;
}

.opinions__scrollbar {
  width: 100%;
  height: 2px;
  background-color: rgba(49, 109, 194, 0.2);
  cursor: pointer;
}

.swiper-scrollbar-drag {
  background-color: #316dc2;
  border-radius: 0;
}

.opinions__buttons {
  position: absolute;
  display: flex;
  gap: 10px;
  z-index: 2;
  right: 15px;
  top: 50%;
  background-color: #f0efe9;
  padding-left: 52px;
  transform: translate(0, -50%);
}

.opinions__button {
  width: 22px;
  height: 22px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;

  &--prev svg {
    color: #316dc2;
    width: 9px;
    height: 9px;
    transform: rotate(-135deg);
  }

  &--next svg {
    width: 9px;
    height: 9px;
    color: #316dc2;
    transform: rotate(45deg);
  }

  &:hover {
    @include hover;
    background-color: $color-default-white;
  }

  &:focus {
    @include focus;
    background-color: $color-default-white;
  }

  &:focus-visible {
    outline: none;
  }

  &:active {
    @include active;
    background-color: $color-default-white;
  }

  &:active svg {
    @include active;
    opacity: 70%;
  }

  &:disabled {
    background-color: transparent;
    cursor: default;
  }

  &:disabled svg {
    color: #a9afb7;
  }
}

@media (max-width: $tablet-width) {
  .opinions {
    padding: 53px 0 110px;

    & h2 {
      max-width: 100%;
      width: 100%;
      font-size: 34px;
      padding: 0 45px;
      letter-spacing: -1.2px;
      margin-bottom: 41px;
    }
  }

  .opinions__slider {
    max-width: 100%;
    margin: 0;
    padding: 0;
    padding-left: 45px;
    margin-bottom: 48px;
  }

  .opinions__slide {
    min-height: 395px;
    max-width: 560px;
    gap: 23px;
  }

  .opinions__slide-data {
    & span {
      font-size: 20px;
      line-height: 24px;
      letter-spacing: -0.6px;
      padding-left: 104px;
    }
  }

  .opinions__slide-terms {
    display: flex;
    gap: 28px;
    flex-wrap: nowrap;
    max-width: 50%;

    & dl {
      display: flex;
      flex-direction: column;
      gap: 8px;
      max-width: 140px;
      margin: 0;

      & dt {
        font-size: 12px;
        line-height: 12px;
        color: #316dc2;
        text-transform: uppercase;
      }

      & dd {
        font-size: 14px;
        line-height: 14px;
        margin: 0;
      }
    }
  }

  .opinions__slide-text {
    display: flex;
    flex-direction: column;
    gap: 15px;
    max-width: 100%;

    & h3 {
      font-size: 20px;
      line-height: 24px;
    }

    & p {
      line-height: 20px;
    }
  }

  .opinions__control {
    padding: 0 45px;
  }

  .opinions__buttons {
    right: 45px;
  }
}

@media (max-width: $mobile-width) {
  .opinions {
    position: relative;
    padding: 34px 15px 18px;

    & h2 {
      font-size: 20px;
      line-height: 20px;
      padding: 0;
      max-width: 80%;
      align-self: flex-start;
      margin-bottom: 33px;
    }
  }

  .opinions__slider {
    padding-left: 0;
  }

  .opinions__slide {
    padding: 42px 15px 25px;
    min-height: 435px;
    max-width: 100%;
  }

  .opinions__slide-data {
    min-height: 60px;
    gap: 25px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: unset;

    & span {
      font-size: 14px;
      line-height: 18px;
      padding-left: 70px;
      max-width: 60%;

      &::before {
        width: 60px;
        height: 60px;
        background-size: 60px 60px;
      }
    }
  }

  .opinions__slide-terms {
    max-width: 100%;
    gap: 29px;

    & dl {
      max-width: 55%;
      gap: 7px;

      & dd {
        font-size: 12px;
      }
    }
  }

  .opinions__slide-text {
    gap: 15px;
    max-width: 100%;

    & h3 {
      font-size: 16px;
      line-height: 18px;
    }

    & p {
      font-size: 14px;
    }
  }

  .opinions__control {
    position: static;
  }

  .opinions__scrollbar {
    display: none;
  }

  .opinions__buttons {
    right: 15px;
    padding-left: 0;
    background-color: transparent;
    top: 33px;
    transform: none;
    gap: 8px;
  }
}
